import { useState } from "react";
import { useContext } from "react";
import { createContext } from "react";
import  Shoppingcart  from "../components/Shoppingcart";

const Shoppingcartcontaext = createContext({});




const Shoppingcartprovaider= ({ children }) => {
  
  const [k, setk] = useState([]);

const cartq = k.reduce(
  (qoan , item  )=>
item.qoan + qoan,0
);
 
const gett = (id) => {
  return k.find((item) => item.id === id)?.qoan || 0;
};

const inn = (id) => {
  setk((k) => {
    if (k.find((item) => item.id === id) == null) {
      return [...k, { id, qoan: 1 }];
    } else {
      return k.map((item) => {
        if (item.id === id) {
          return { ...item, qoan: item.qoan + 1 };
        } else {
          return item;
        }
      });
    }
  });
};

const dec = (id) => {
  setk((k) => {
    if (k.find((item) => item.id === id) == null) {
      return k.filter((item) => item.id !== id);
    } else {
      return k.map((item) => {
        if (item.id === id) {
          return { ...item, qoan: item.qoan - 1 };
        } else {
          return item;
        }
      });
    }
  });
};

const remove = (id) => {
  setk((K) => K.filter((item) => item.id !== id));
};

  
return (
<Shoppingcartcontaext.Provider value={{ k, 
   gett,
   inn,
   dec,
   remove,
   }}>
    {children}
    <Shoppingcart/>
  </Shoppingcartcontaext.Provider>
  );
};


export default Shoppingcartprovaider;

export const useShoppingcart = () => {
  return useContext(Shoppingcartcontaext);
};
