import React from "react";
import Cartitem from "./Cartitem";
import format from "./format";
import "../components/Shoppingcart.css";
import { useShoppingcart } from "./shop";
import { myprojects } from "./3-main/maincode";
import "../components/Shoppingcart.css";
import Main from "../components/3-main/main";
import k from "../components/shop";


import html2canvas from "html2canvas";

export default function Shoppingcart() {
  const { k } = useShoppingcart();

  const takeScreenShot = () => {
    const element = document.getElementById("divToTakeScreenShotOf");
    if (!element) {
      return;
    }
    html2canvas(element, {
      backgroundColor: "#0000",
    })
      .then((canvas) => {
        let image = canvas.toDataURL("image/jpeg");
        const a = document.createElement("a");
        a.href = image;
        a.download = "capture.jpeg";
        a.click();
      })
      .catch((err) => {
        console.error("we are can not");
      });
  };

  return (
    <div className="ff " >
      <button className="bot" onClick={takeScreenShot}>
        {" "}
        <p>       </p>
      </button>
      <div id="divToTakeScreenShotOf">
        <div className="bot1">
          <div className="all2">
            <div className="header"></div>
            <div className="title">  
            
                      50      
            <br/>
            <br/>
            <br/>
                     150    
               
          <br/> 
                  15      

            </div>
            <div className="body">
              {k.map((item) => (
                <Cartitem key={item.id} {...item} />
              ))}

              <div className="body2"></div>
              <div className="body3">
              {format(
            k.reduce((total, k) => {
              const item = myprojects.find((i) => i.id === k.id);
              return total + (item?.price || 0) * k.qoan*17000;
            }, 0)
          )}
              </div>
            </div>
 
          </div>
        </div>
      </div>
      <div className="all">
        <div className="header"></div>
        <div className="title">

    <h3></h3>


        </div>
        <div className="body">
          {k.map((item) => (
            <Cartitem key={item.id} {...item} />
          ))}
          {format(
            k.reduce((total, k) => {
              const item = myprojects.find((i) => i.id === k.id);
              return total + (item?.price || 0) * k.qoan*17000;
            }, 0)
          )}
          <div className="body2"></div>
        </div>
      </div>
    </div>
  );
}
