import React from "react";
import "./contacts.css";



import contactanimation from "../../animation/contact.json";

import doneanimation from "../../animation/vew.json";

import Shoppingcart from "../Shoppingcart";
import Cartitem from "../Cartitem";
import Shoppingcartprovaider from "../shop";

import {useForm, ValidationError} from "@formspree/react"







export default function Contacts() {
  const [state, handleSubmit] = useForm("xeqyapwz")
  if (state.succeeded) {
    return <h4>   ..     ....     </h4>;
}

  return (

    <section className=" contacts-us "  >

      <p className="title">
        <span className="icon-envelope"></span>
              
     </p>

      <p className="subtitle vv"> 
      
    
      <br/>
      
      
       -2- :        

        </p>


      <div className="flex bb">



        <form onSubmit={handleSubmit}>



        <div className="fle">
            <label style={{ marginTop:"20px" ,marginLeft:"170px"}} htmlfor="message"> </label>
            <input  className="pp" style={{ marginTop:"30px" ,marginBottom:"10px"}} required type="message" name="name" id="email" />
            <ValidationError 
             prefix="Message" 
            field="message"
             errors={state.errors}
    />
            </div>






          <div className="fle">
            <label style={{width:"150px", marginTop:"20px",marginLeft:"170px"}} htmlfor="message">     </label>
            <input style={{ marginTop:"30px",marginBottom:"10px"}} required type="message" name="number" id="email" />
            <ValidationError 
             prefix="Message" 
            field="message"
             errors={state.errors}
    />
          </div>



             <div className="fle">
            <label style={{width:"150px", marginTop:"20px",marginLeft:"170px"}} htmlfor="message"> </label>
            <input style={{ marginTop:"30px",marginBottom:"10px"}} required type="message" name="location" id="email" />
            <ValidationError 
             prefix="Message" 
            field="message"
             errors={state.errors}
    />
            </div>



                
           <div className="fle">
             <label className="h" style={{width:"150px", marginTop:"20px",marginLeft:"170px"}} htmlfor="email">    </label>
             <input className="hh" style={{ marginTop:"30px",marginBottom:"10px",}}required type="text1" name="nots" id="email" placeholder="      "/>
            <ValidationError 
             prefix="Message" 
            field="message"
             errors={state.errors}
    />
          
           </div>


    
          <button type="submit" disabled={state.submitting}
          
          className="submit"></button>



              

        </form>


    



      </div>









    </section>








  )


}


