import React from 'react';
import {myprojects} from "../components/3-main/maincode";
import format from './format';
import cartq from './shop';
import { useShoppingcart } from './shop';

export default function Cartitem({id , qoan}) {
  const dolar = 17000;
  const {remove} = useShoppingcart();
  const item = myprojects.find((i)=>i.id === id);
  if(item == null )return null;

  return (
    <div className="stack">

<div className="f">
  <div className="f1">

       {item.namee}
       {qoan > 1 && (

        <span className='f2'>X{qoan}</span>
       )}

  </div>

  <div className="price">
       <p>  </p> 

    {format(item.price*dolar)}
  </div>

  <div className="price2">
  {format(item.price*qoan*dolar)}
 
  </div>

  {cartq}
</div>

<button className='g' onClick={() => {
        
        remove(id);


      }}>&times;</button>
    </div>
    
    
  )
}
